@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
/************** GLOBAL **************/
:root {
  --gray: #c0bcb5;
  --white: #eeeeee;
  --black: #1c2321;
  --lightblue: rgb(128, 189, 255);
  --green1: rgb(15, 132, 28);
  --footer: rgb(29, 63, 56);
  --niceBlue: rgb(36, 82, 69);
}
html {
  width: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  scroll-behavior: smooth;
  min-width: 100%;
  min-height: 100%;
}

* {
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  z-index: 1;
}

/***************** LANDING PAGE *****************/
.mainPage {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: rgb(15, 132, 28);
  background-color: var(--green1);
  position: relative;
  top: -80px;
  overflow: hidden;
  min-height: 600px;
}
.mainPage a {
  margin-top: 20px;
}
.overlayPicture {
  height: auto;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0.2;
  z-index: 0;
}

.mainPage h1 {
  font-weight: 900;
  font-size: 4rem;
}
#firstButton {
  padding: 10px;
  width: 300px;
  border-radius: 2%;
  text-decoration: none;
  color: white;
  border: white solid 2px;
  transition: 0.5s;
  text-align: center;
  margin-left: 40vw;
  z-index: 2;
}
#firstButton:hover {
  background-color: white;
  color: rgb(1, 165, 1);
  transition: 0.5s;
}
.textTotal {
  margin-top: 40vh;
  margin-left: 40vw;
  z-index: 2;
}
@media (max-width: 900px) {
  #firstButton {
    margin-left: 0;
  }
  .textTotal {
    margin-left: 0;
  }
  .overlayPicture {
    display: none;
  }
}

.welcomeText {
  color: white;
  max-width: 700px;
  width: 100%;
  margin-top: 30px;
}

/************ PROJECTS PAGE ***************/

.projects {
  top: -100px;
  text-align: center;
  height: 120vh;
  min-height: 850px;
  width: 100%;
  position: relative;
  background-color: rgb(29, 63, 56) !important;
  background-color: var(--footer) !important;
  color: white !important;
  overflow: hidden;
}

.projectButton {
  background-color: transparent !important;
  color: rgb(1, 165, 1) !important;
  opacity: 0.8;
  transition: 0.5s;
  width: 80%;
  align-self: center;
  border: 2px solid rgb(1, 165, 1) !important;
}

.projectButton:hover {
  background-color: rgb(15, 132, 28) !important;
  background-color: var(--green1) !important;
  color: white !important;
  opacity: 1;
  transition: 0.5s;
}

.projectImage {
  height: 200px;
  width: auto;
  border-radius: 30%;
  border: 0.5px solid grey;
  align-self: center;
  margin-bottom: 10px;
}

.projectCard {
  margin-top: 50px;
  margin-bottom: 10px;
  padding: 5px;
  background-color: transparent !important;
  border: none !important;
  transition: 0.5s;
  opacity: 0.7;
  position: relative;
}
.projectCard:hover {
  padding: 0;
  opacity: 1;
  transition: 0.5s;
}
@media (max-width: 1200px) {
  .projects {
    height: 130vh;
  }
}

@media (max-width: 575px) {
  .projects {
    height: 250vh;
  }
}
@media (max-height: 650px) {
  .projects {
    height: 140vh;
  }
  .mainPage {
    height: 110vh;
  }
}
@media (max-height: 400px) {
  .projects {
    height: 200vh;
  }
}

/************* CONTACT PAGE **************/

.contactPage {
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  text-align: center;
  background-color: rgb(29, 63, 56);
  background-color: var(--footer);
  position: relative;
  margin-top: 350px;
}

.contactPage h2 {
  font-size: 1.5rem;
  color: white;
}

#social-buttons {
  display: inline-block;
  margin-top: 50px;
}
.fab {
  text-decoration: none !important;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  align-self: center;
  text-align: center;
  margin-right: 11px;
  padding: 15px;
}
.fab:hover {
  opacity: 0.6;
  transition: 0.5s;
  text-decoration: none !important;
  color: white;
}

.fa-twitter {
  background: #55acee;
  color: white;
  text-decoration: none !important;
}
.fa-linkedin {
  background: #55acee;
  color: white;
}
.fa-github {
  background: #f1502f;
  color: white;
}
.fa-codepen {
  background: black;
  color: white;
}

#paperPlane {
  width: 300px;
  height: 50px;
  border: 2px solid rgb(1, 165, 1);
  border-radius: 0%;
  padding: 10px;
  width: 300px;
  color: rgb(1, 165, 1) !important;
  font-size: 1.5rem;
  transition: 0.5s;
  margin-top: 0;
  text-align: center;
  margin-left: 10px;
}
#paperPlane:hover {
  background-color: rgb(1, 165, 1);
  color: white !important;
  opacity: 1;
  transition: 0.5s;
  text-decoration: none !important;
}
#email-me {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}

/************ FOOTER **************/
.footer {
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
  margin-top: 20px;
  height: 100px;
  background-color: #1c2321;
  background-color: var(--black);
  color: grey;
  font-size: 0.8rem;
  margin: 0;
  display: grid;
  place-items: center;
}
.footerText {
  margin: 0;
  padding: 0;
}

/************ MOBILE **************/
@media only screen and (max-device-width: 480px) {
  .overlayPicture {
    height: 100vh;
    width: auto;
    overflow: hidden;
  }
  .mainPage {
    height: 100vh;
  }
  .projects {
    height: 50vh;
  }
}

/****************NAVBAR************/

:root {
  --gray: #c0bcb5;
  --white: #eeeeee;
  --black: #1c2321;
  --blue: #83dfe9;
  --purple: #846075;
  --red: #af5d63;
  --green: rbg(96, 108, 56);
}

.navBar {
  height: 80px;
  background-color: transparent;
}
.navBar:hover {
  opacity: 1 !important;
  transition: 0.5s;
}

#logo {
  border: 2px dotted white;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: grid;
  place-items: center;
}
.navLink {
  color: white !important;
}
.navLinks {
  color: white !important;
}

.navLink:hover {
  border-bottom: 1px solid white;
  color: white !important;
}

@media (max-width: 900px) {
  .navLink {
    color: gray !important;
    opacity: 1;
  }
  .navLinks {
    color: gray !important;
    opacity: 1;
  }
  .navLink:hover {
    border-bottom: 1px solid lightgray;
    color: lightgray !important;
  }
}

/*************** ABOUT PAGE ***************/

:root {
  --niceBlue: rgb(36, 82, 69);
}
.aboutPage {
  display: flex;
  flex-direction: column;
  height: 565px;
  background-color: transparent;
  position: relative;
  top: -12vh;
}

.aboutSection {
  height: 500px;
  width: 100%;
  display: grid;
  place-items: center;
}

/*************FIRST SECTION*************/

.pictureAndBox {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  position: relative;
  min-height: 200px;
  margin-top: 50px;
  margin-bottom: 150px;
}

.aboutPicture {
  max-height: 350px;
  max-width: 300px;
  border-radius: 50%;
  position: relative;
}

.skillBox {
  width: 90%;
  background-color: transparent;
  margin-left: 150px;
  display: flex;
  flex-direction: row;
}
@media (max-width: 1017px) {
  .skillBox {
    display: flex;
    flex-direction: column;
    margin-top: 0px;
    background-color: transparent;
    margin-left: 50px;
  }
  .techIcons {
    margin-bottom: 30px;
  }
  .aboutPicture {
    margin-top: 50px;
  }
}
@media (min-width: 900px) {
  .techIcons {
    margin-right: 45px;
  }
}
.techIcons {
  margin-right: 60px;
}
.techIcons:hover {
  opacity: 1 !important;
  color: black;
}
/****************COLLAGE SECTION********************/
.collage {
  position: relative;
  max-width: auto;
  height: 700px;
  opacity: 0.2;
  overflow: hidden;
  text-align: center;
}
.collageSection {
  background-color: rgb(36, 82, 69);
  background-color: var(--niceBlue);
  display: grid;
  place-items: center;
  overflow: hidden;
  position: relative;
  top: 300px;
}
.aboutMe {
  color: white;
  justify-self: center;
  text-align: center;
  top: 79px;
  position: absolute;
  text-align: center;
}
.aboutMe h1 {
  margin-bottom: 100px;
  font-size: 5rem;
  font-weight: 800;
}
.aboutMe p {
  font-size: 1.3rem;
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
  width: 98%;
}

@media (max-width: 900px) {
  .aboutMe h1 {
    font-size: 3rem;
    font-weight: 800;
  }
  .aboutMe p {
    font-size: 0.8rem;
    margin-top: 20px;
    width: 90%;
  }
}

/**************MOBILE*************/

@media only screen and (max-device-width: 480px) {
  .aboutMe {
    top: 50px;
  }
  .collage {
    height: 40vh;
  }
  .collageSection {
    top: 600px;
  }
  .pictureAndBox {
    top: 150px;
  }
}

